import React from 'react';
import logo from '../../../Auth/images/logoLoading.svg';
import styles from '../SingInForm/SingInForm.module.scss';
import WithMaterialUI from './WithMaterialUI/WithMaterialUI';

export default () => {
	return (
		<div className={styles.card}>
			<img src={logo} alt="logo" className={styles.imgLogo} />
			<div className={styles.actionContainer}>
				<WithMaterialUI />
			</div>
		</div>
	);
};
