import React, { useState } from 'react';
import { useFormik } from 'formik';
import styles from '../SingInForm.module.scss';
import { InputAdornment, styled, TextField } from '@mui/material';
import { useHistory } from 'react-router-dom';
import * as yup from 'yup';
import authState from '../../../../../store/authState';
import CloseEyeIcon from './CloseEyeIcon';
import OpenEyeIcon from './OpenEyeIcon';

const StyledTextField = styled(TextField)({
	'& .MuiInputLabel-root': {
		right: 0,
		top: 10,
		shrink: true,
	},
	'& .MuiInputLabel-shrink': {
		display: 'none',
	},
	'& .MuiOutlinedInput-root.Mui-focused': {

		'& legend ': {
			display: 'none',
		},
	},
	'& .MuiInputBase-root': {
		height: 81,

		color: '#B8B8B8',

		fontSize: '24px',
		fontFamily: 'Inter',
		fontWeight: 400,

		marginLeft: '16px',
		marginRight: '16px',
	},

	'& input:-webkit-autofill': {
		fontSize: '24px !important',
	},

	'& .MuiInput-underline:after': {
		border: 'none',
	},
	'& .MuiOutlinedInput-root': {
		'& fieldset': {
			border: 'none',
		},
		'&:hover fieldset': {
			border: 'none',
		},
		'&.Mui-focused fieldset': {
			border: 'none',
		},
	},
});

export default () => {
	const [isHidden, setHidden] = useState(true);

	const history = useHistory();
	const toggleShow = () => {
		setHidden(!isHidden);
	};

	const validationSchema = yup.object({
		password: yup
			.string('Enter your password')
			.min(4, 'Password should be of minimum 8 characters length')
			.required('Password is required'),
	});

	const formik = useFormik({
		initialValues: {
			email: '',
			password: '',
		},
		validationSchema: validationSchema,
		onSubmit: (values) => {
			const { email, password } = values;
			
			if (email === 'admin' && password === 'admin') {
				authState.setAutorize(true);
				history.push('/port');
			}
		},
	});

	return (
		<form onSubmit={formik.handleSubmit} className={styles.actionContainer}>
			<StyledTextField
				className={styles.textfieldStyle}
				autoComplete='off'
				variant='outlined'
				margin='normal'
				fullWidth
				placeholder='Login'
				id="email"
				name="email"
				autoFocus
				value={formik.values.email}
				onChange={formik.handleChange}
				error={Boolean(formik.errors.email) && formik.touched.email}
				helperText={formik.touched.email && formik.errors.email}
			/>

			<StyledTextField
				variant="outlined"
				margin="normal"
				className={styles.textfieldStyle}
				InputProps={{
					style: { color: '#B8B8B8' },
					endAdornment: (
						<InputAdornment position="end" onClick={toggleShow}>
							{isHidden ? <CloseEyeIcon /> : <OpenEyeIcon />}
						</InputAdornment>
					),
				}}
				InputLabelProps={{
					style: { color: '#B8B8B8' },
				}}
				name="password"
				label="Password"
				type={isHidden ? 'password' : 'text'}
				id="password"
				error={Boolean(formik.errors.password) && formik.touched.password}
				helperText={formik.touched.password && formik.errors.password}
				onChange={formik.handleChange}
			/>

			<div className={styles.downContainer}>
				<button className={styles.downAction} type="submit">
					Login
				</button>
				<div className={styles.downActionHelp}>Contact With Us</div>
			</div>
		</form>
	);
};
